import React from 'react';

const s_svg = {
	width: "100%",
	height: "100%",
};

const s_node_metabolite = {
	fill:"orange",
	strokeWidth:"1px",
	stroke:"black"
};

const s_node_multimarker = {
	fill: "aliceblue",
	strokeWidth:  "1px",
	stroke: "black"
};

const s_node_midmarker = {
	fill: "lightgreen",
	strokeWidth:  "1px",
	stroke: "black"
};

const s_segment = {
	stroke: "lightgray",
	strokeWidth: 10,
	fill:"none"
};

class Escher extends React.Component {
	render() {
		{console.time("renderescher")}
		let {reactions, nodes, text_labels, canvas} = this.props.Data;
		console.log(this.props);
		return <svg style={s_svg} viewBox={canvas.x + " " + canvas.y + " " + canvas.width + " " + canvas.height}>
			<g id="Reactions">
				{console.time("reactions")}
				{Object.entries(reactions).map(function ([key, reaction]) {
					return Object.values(reaction.segments).map(function (segment) {
						let from_node = nodes[segment.from_node_id],
								to_node = nodes[segment.to_node_id],
								b1 = segment.b1;
						if (segment.b1) {
							let b2 = segment.b2;
							return <path d={"M" + from_node.x + "," + from_node.y + "C" + b1.x + "," + b1.y + " " + b2.x + "," + b2.y + " " + to_node.x + "," + to_node.y}
								 style={s_segment} />
						} else {
							return <line x1={from_node.x} y1={from_node.y} x2={to_node.x} y2={to_node.y} style={s_segment} />;
						}
					});
				})}
				{console.timeEnd("reactions")}
			</g>
			<g id="Nodes">
				{console.time("nodes")}
				{Object.entries(nodes).map(function ([key, node]) {
					switch(node.node_type) {
						case "metabolite":
							return <g key={key} data={node}>
								<circle cx={node.x} cy={node.y} r={10+10*(+node.node_is_primary)} style={s_node_metabolite} />
								<text x={node.label_x} y={node.label_y} fontSize="3em">{node.bigg_id}</text>
							</g>;
						case "multimarker":
							return <circle key={key} cx={node.x} cy={node.y} style={s_node_multimarker} r="10" />;
						case "midmarker":
							return <circle key={key} cx={node.x} cy={node.y} style={s_node_midmarker} r="10" />;
						default:
							console.error("Unknown type of node",node);
					};
				})}
				{console.timeEnd("nodes")}
			</g>
			{console.timeEnd("renderescher")}
		</svg>
	};
}

export default Escher;



